import React from 'react';
import facebook from '../../assets/image/facebook.png'

export default class FbLogin extends React.Component {

    componentDidMount() {
        document.addEventListener('FBObjectReady', this.initializeFacebookLogin);
    }

    componentWillUnmount() {
       document.removeEventListener('FBObjectReady', this.initializeFacebookLogin);
    }

    //Init FB object and check Facebook Login status
    initializeFacebookLogin = () => {
        this.FB = window.FB;
        this.checkLoginStatus();
    }

    //Check login status
    checkLoginStatus = () => {
        this.FB.getLoginStatus(this.facebookLoginHandler);
    }

    //check api and login
    facebookLogin = () => {
        if (!this.FB) return;
        
        this.FB.getLoginStatus(response => {
            if (response.status === 'connected') {
                this.facebookLoginHandler(response);
            } else {
                this.FB.login(this.facebookLoginHandler, { scope: 'public_profile' });
            }
        });
    }

    //Hanfle login response
    facebookLoginHandler = response => {
        if (response.status === 'connected') {
           
              this.FB.api('/me', userData => {
                let result = {
                  ...response,
                  user: userData
                };
                // console.log(result)
                this.props.fblogin(true, result);
              });
        } else {
              this.props.fblogin(false);
        }
    }


    render() {
        // let { children } = this.props;
        return (
            <div className="facebook"><a href="#" onClick={this.facebookLogin}>
              <img src={facebook} alt="facebook" className="facebook_icon" />
              </a>
              
            </div >
        )
    }
}